
import { defineComponent, reactive, toRefs, computed } from 'vue'
import { useRouter } from 'vue-router'
import { getRecommend } from '@/service/recommend'
import { ALBUM_KEY } from '@/assets/ts/constant'
import { cache } from '@/assets/ts/array-store'
import { Sliders } from '@/service/interface'
import Slider from '@/components/base/slider/slider.vue'
import Scroll from '@/components/base/scroll/scroll.vue'

interface Album {
  [key: string]: any
}

interface State {
  sliders: Sliders[];
  albums: Album[];
  loading: boolean;
  selectedAlbum: Album;
}

export default defineComponent({
  name: 'recommend',
  components: {
    Slider,
    Scroll
  },
  setup () {
    const router = useRouter()
    const state = reactive({
      sliders: [],
      albums: [],
      loading: computed((): boolean => {
        return !state.sliders.length && !state.albums.length
      }),
      selectedAlbum: {}
    }) as State

    // 获取页面数据
    const getRecommendRes = () => {
      getRecommend().then((res) => {
        state.sliders = res.sliders
        state.albums = res.albums
      })
    }
    getRecommendRes()

    // 点击歌单
    const selectItem = (album: Album) => {
      state.selectedAlbum = album
      // 缓存点击的歌单
      cache(ALBUM_KEY, album)
      router.push({
        path: `/recommend/${album.id}`
      })
    }

    return {
      ...toRefs(state),
      selectItem
    }
  }
})
